.containerOne {
  background-color: #45789d;
  padding: 24px;
  position: relative;
}

.mainOne {
  width: 75%;
  margin-left: 20%;
  background-color: #F1FAEE;
  height: 100%;
  border-radius: 1.5rem;
  padding: 24px;
}

.navigation {
  position: absolute;
  top: 24px;
  left: 24px;
  width: 15%;
  height: 1200px;
  background-color: #F1FAEE;
  border-radius: 1.5rem;
  padding: 64px 24px 24px;
}

.navDiv {
  padding: 16px 0;
  text-align: center;
}

.nav-link {
  font-size: 24px;
  text-decoration: none;
  color: #1d3557;
}

.nav-link.active {
  color: #2a9d8f;
  font-weight: bold;
  border-bottom: 4px solid #2a9d8f;
}

.fr-info {
  font-size: 3rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  padding: 24px 24px 24px 48px;
  color: #1d3557;
  /*text-transform: capitalize;*/
}

.firstRow {
  display: flex;
  align-items: center;
  padding: 64px 0;
}

.width-60 {
  width: 60%;
}

.width-40 {
  width: 40%;
}

.fullWidth {
  width: 100%;
}

.img-block {
  text-align: center;
}

.sec-row {
  background-color: #1d3557;
  color: #f1faee;
  border-radius: 0.75rem;
  padding: 64px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 28px;
  text-align: justify;
}

.sec-row p {
  padding: 16px;
}

.centered {
  margin: 3rem auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.linkSel {
  text-decoration: underline;
  font-weight: bold;
}

.third-row {
  display: flex;
  flex-wrap: wrap;
  padding: 96px 24px 0;
}

.card-box {
  width: 50%;
  text-align: center;
  height: 600px;
}

.card {
  width: 400px;
  height: 450px;
  background-color: #ffddd2;
  border-radius: 20px;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.2);
  margin: 3rem auto;
  flex-grow: 1;
  flex-shrink: 1;
  transition: width 500ms ease 0s;
}

.card:hover {
  box-shadow: 4px 4px 4px 8px rgba(0, 0, 0, 0.2);
  width: 420px;
  height: 470px;
  cursor: pointer;
}

.card a {
  text-decoration: none;
}

.card p {
  font-size: 32px;
  padding: 24px;
  color: #1d3557;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.card-icon {
  width: 256px;
  margin-top: 40px;
}

.intro-cs {
  text-align: center;
  font-size: 40px;
  color: #1d3557;
  margin-bottom: 64px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.summaryOne {
  font-size: 24px;
  font-weight: bold;
  padding-bottom: 16px;
  font-style: italic;
}

.cr-fi {
  font-size: 20px;
}

footer {
  width: 75%;
  margin-left: 20%;
  background-color: #fca311;
  height: 100%;
  border-radius: 1.5rem;
  padding: 24px;
  margin-top: 64px;
  color: #fff;
}

.footer-info {
  font-size: 1.5rem;
}

.footer-info p {
  padding: 1rem;
}

.credits {
  padding-left: 16px;
}

.credits a {
  /*text-decoration: none;*/
  display: block;
  padding-bottom: 8px;
  font-size: 20px;
  color: #023047;
}

.csOne {
  min-height: 660px;
  height: 100%;
  color: #1d3557;
}

.backLink {
  font-size: 28px;
  font-weight: bold;
  padding: 16px 16px 16px 24px;
}

.backLink a {
  text-decoration: none;
  color: #1d3557;

}

.csOneHeader, .csTwoHeader, .cs3Header, .cs4Header {
  font-size: 40px;
  padding: 16px 16px 32px 24px;
}

.csOneBody {
  padding: 0 24px;
}

.csOneH {
  font-size: 32px;
  padding-bottom: 8px;
  border-bottom: 2px solid #1d3557;
  margin-bottom: 16px;
  display: inline-block;
}

.csOneC {
  font-size: 28px;
}

.pd-bt {
  padding-bottom: 48px;
}

.pd-bt-small {
  padding-bottom: 24px;
}

.clrp {
  width: 100px;
  height: 40px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  margin-right: 24px;
}

.fir {
  background-color: #0070D2;
}

.sec {
  background-color: #16325C;
}

.thi {
  background-color: #526173;
}

.fou {
  font-family: sans-serif;
  padding-right: 24px;
}

.dp-flx {
  display: flex;
  margin: 16px 40px;
  align-items: center;
}

.flx-bx {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 48px;
}

.flx-bx div {
  font-size: 32px;
}

.mrDesk {
  /*width: 800px;*/
  width: 64%;
}

.last-text {
  font-size: 28px;
  color: #f07167;
  margin: 0 64px 24px;
  text-align: center;
}

.csTwo {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.csTwoHeader {
  color: #e76f51;
}

.csTwoPara {
  color: #0081a7;
  font-size: 28px;
  padding-bottom: 8px;
}

.padd-div {
  padding: 16px 24px 56px;
}

.csTwoRowTwo {
  color: #c81d25;
  font-size: 32px;
  padding-bottom: 16px;
  border-bottom: 2px solid #c81d25;
  margin-bottom: 48px;
}

.textCen {
  text-align: center;
}

.screenName {
  font-size: 24px;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 0px;
  background-color: #284b63;
  color: #fff;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 8px 0;
}

.scrIcon {
  width: 80%;
  margin: 8px 0 64px;
}

.cs3 {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.cs3Header {
  color: #006d77;
  border-bottom: 2px solid #006d77;
  display: inline-block;

}


.right-content {
  text-align: right;
  margin: 64px 0;
}

.exp-para {
  font-size: 40px;
  display: inline-block;
  background-color: #52b69a;
  padding: 8px 16px;
  border-radius: 20px;
  color: #fff;
  font-weight: bold;
  margin-bottom: 24px;
}

.exp-header {
  display: inline-block;
  font-size: 40px;
  color: #14746f;
  padding-bottom: 8px;
  border-bottom: 2px dotted #14746f;
  margin-bottom: 8px;
}

.flexExp {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.iconText {
  padding: 24px;
  font-size: 32px;
  color: #00afb9;
  width: 40%;
}

.expIcon {
  margin: 64px 0;
  /*width: 900px;*/
  width: 60%;
}

.last .exp-para {
  font-size: 28px;
}

.cs4 {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.cs4Header {
  text-align: center;
  color: #ff5400;
  font-weight: bold;
}

.quoIcon {
  width: 900px;
}

.quo-pack {
  text-align: center;
}

.cs4-para {
  text-align: center;
  font-size: 28px;
  color: #bd1f36;
  padding-bottom: 12px;
}

.mrg-blk {
  margin: 64px 0;
}

.qIconTxt {
  color: #003e1f;
  font-size: 24px;
  padding: 8px 0;
  border-bottom: 2px solid #003e1f;
  margin-bottom: 64px;
}

.mrg-last-blk {
  margin: 64px 0;
  background-color: #dc143c;
  padding: 36px;
  color: #fff;
  border-radius: 36px;
  font-size: 32px;
}

.mrg-last-blk p {
  padding-bottom: 2rem;
}

.avtr-img {
  width: 90%;
}

